
        const data = [{"#":"","title":"ARtillery","---":"","description":"\\n\\nЗадача игрока — продержаться как можно дольше, отстреливаясь от противников, появляющихся в пространстве вокруг него. Для защиты игрок может перемещать и наклонять телефон, а также сбивать летящие в него снаряды. Для стрельбы нужно коснуться экрана. Со временем сложность увеличивается. Существует три типа противников с разными моделями поведения и сложностью, каждый из которых появляется реже или чаще других.\\n\\nПриложение реализовано с использованием ARCore, что позволяет ему работать без привязки к <span style=\"font-style:italic;\">печатным</span> меткам.\\n\\n<small>\\nПечатные метки — это распечатанные на бумаге или нарисованные ярким цветом специальные символы, которые являются опорным маркером для камеры. <a target=\"_blank\" href=\"https://habr.com/ru/post/563666/?\">Подробнее</a>\\n\\nРанняя версия приложения была реализована с использованием Vuforia, для работы требовалось распечатать метку.\\n</small>","images":["/images/projects/ARtillery/resized_ARtillery_Moment-min.webp","/images/projects/ARtillery/resized_ARtillery_Moment3.webp","/images/projects/ARtillery/resized_ARtillery_Moment2-min.webp","/images/projects/ARtillery/ARtilleryAction.gif"],"videos":[],"tags":["GameDev","Игра","AR","Unity","Android","Mobile"],"tech":["Unity"],"developers":["Поздняков Д.В."],"sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/ARtillery"}],"link":"ARtillery"},{"#":"","title":"ArtWay","---":"","description":"\\n\\nArtWay - это веб-сервис, который позволяет посетителю выставки, музея или другой экспозиции виртуально проходить по залам или другим помещениям, переходить к экспонатам, расположенным в этих залах, по ID, QR-кодам или значкам, расположенным как в залах, так и на экспонатах, а также получать информацию об этих экспонатах в электронном виде. в виде текста, картинок, аудиогидов и ссылок.\\n\\nСайт: <a target=\"_blank\" href=\"https://artway.rtuitlab.dev\">artway.rtuitlab.dev</a>","images":["/images/projects/ArtWay/resized_img.webp","/images/projects/ArtWay/resized_img_1.webp","/images/projects/ArtWay/resized_img_2.webp","/images/projects/ArtWay/resized_img_3.webp","/images/projects/ArtWay/resized_img_4.webp","/images/projects/ArtWay/resized_img_5.webp"],"videos":[],"tags":["Frontend","Backend","ML","Экскурсии"],"tech":["React.js","TypeScript","Django","PostgreSQL","PyTorch"],"developers":["Ченцов А.Б.","Подольский К.В.","Снегирёв Д.А.","Корольков А.Д.","Миронов Н.М.","Макущенко М.А."],"site":"","sourcecode":[],"link":"ArtWay"},{"#":"","title":"Прототип большого робота квадрупеда","---":"","description":"\\n\\nПрототип робота подобного размера был разработан для тестирования выполнения задач мобильной поддержки пехоты.\\n\\nОгромный аккумуляторный блок обеспечивает высокую энергетическую автономность. Запуск робота происходит через блок плавного пуска. Управление же всеми двигателями выполняется через ESP32, с которым можно связываться через мобильное приложение по Bluetooth. Все 12 двигателей управляются через единую CAN-шину.","images":["/images/projects/BigDog/resized_img1.webp","/images/projects/BigDog/resized_img2.webp"],"videos":[],"tags":["Smart devices","Робототехника","Embedded"],"tech":["CAD","С/C++","FreeRTOS"],"developers":["Дергунов А.А.","Нечипоренко А.Ю.","Журавлёв П.Р."],"sourcecode":[{"name":"Прошивка","link":"https://github.com/ShiWarai/RDB-1-controller"},{"name":"Android-приложение","link":"https://github.com/ShiWarai/RDB-1-remote-control-android"}],"link":"BigDog"},{"#":"","title":"Многопользовательская сборка турбины самолёта","---":"","description":"\\n\\n\\nИгроки собирают турбовентиляяторный двигатель самолёта серии CFM-56 в VR-пространстве.\\n\\nКаждый этап сборки сопровождается руководствами и подсказками, все детали документированы и подробно описаны. Собранная модель анимирована и демонстрирует принципы своей работы.\\n\\nПроект является демонстрацией процесса обучения специалистов по оборудованию в VR-пространстве: погружение позволяет быстро освоить материал без риска порчи настоящего оборудования и получения травм.\\n\\n","images":["/images/projects/Engine-VR/resized_s4.webp","/images/projects/Engine-VR/resized_s3.webp","/images/projects/Engine-VR/resized_s2.webp","/images/projects/Engine-VR/resized_s1.webp","/images/projects/Engine-VR/resized_141155619-bb28a0e8-fd3b-4359-97e5-36f3d1cf71c3.webp","/images/projects/Engine-VR/resized_141160470-a31d20dd-86bd-4be8-9308-a631227b5ba0.webp","/images/projects/Engine-VR/resized_141155697-f247130d-e9a9-4f7d-812c-7a328e3022af.webp","/images/projects/Engine-VR/resized_141155722-2e5c8eab-dfef-42f7-ae67-daf43e1756d6.webp"],"videos":[],"tags":["VR","Взаимодействие","Образовательная"],"tech":["Unity","C#"],"developers":["Сиротенко М.Р.","Корчиков М.Д.","Смирнов М.А","Лысаков И.А.","Смирнов Я.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/Engine-VR"}],"link":"Engine-VR"},{"#":"","title":"Система локальной поддержки Epson","---":"","description":"\\n\\nСистема локальной поддержки обучения и распознования компонентов вычислительной техники, помогает студентам инженерных специальностей знакомиться с компонентами вычислительной техники.\\n\\nСпециализированная система с возможностью:\\n\\n– довести до пользователя справочную информацию о наблюдаемом объекте,\\n– предложить подсказки по взаимодействию с предметом,\\n– управлять контентом через машинного зрение","images":["/images/projects/Epson/resized_img.webp","/images/projects/Epson/resized_img_1.webp","/images/projects/Epson/resized_84.webp","/images/projects/Epson/resized_86.webp","/images/projects/Epson/resized_87.webp","/images/projects/Epson/resized_88.webp"],"videos":[],"tags":["Mobile","ML","Образовательная деятельность","AR","QR-коды","SDK Epson","Android","Kotlin","Java"],"tech":["Android","Kotlin","Java","SDK Epson","Python","TensorflowLite"],"developers":["Запорожских А.И."],"site":"","sourcecode":[{"name":"Репозиторий","link":"https://github.com/MrApple100/EpsonPlata"}],"link":"Epson"},{"#":"","title":"Система удалённой поддержки Epson","---":"","description":"\\n\\nСистема удалённой поддержки обучения и распознования компонентов вычислительной техники, с возможностью подключения оператора.\\n\\nВ данной работе будет рассматриваться идея удаленного оператора, который может дистанционно подсказывать оператору порядок действий, и сможет передать свои знания для наладки, сборки и диагностики оборудования.\\n\\nСпециализированная система с возможностью:\\n\\n– Удалённая передача данных,\\n– Работа с удалённым оператором,\\n– Довести до пользователя справочную информацию о наблюдаемом объекте,\\n– Предложить подсказки по взаимодействию с предметом,","images":["/images/projects/EpsonWiFi/resized_img_1.webp","/images/projects/EpsonWiFi/gif1.gif","/images/projects/EpsonWiFi/gif2.gif","/images/projects/EpsonWiFi/gif3.gif","/images/projects/EpsonWiFi/gif4.gif","/images/projects/EpsonWiFi/gif5.gif"],"videos":[],"tags":["Mobile","Образовательная деятельность","Удалённая поддержка","AR","QR-коды","SDK Epson","Android","Kotlin","Java","H264","VideoStreaming"],"tech":["Android","Kotlin","Java","SDK Epson","H264"],"developers":["Запорожских А.И."],"site":"","sourcecode":[],"link":"EpsonWiFi"},{"#":"","title":"Виртуальные экскурсии по РТУ МИРЭА","---":"","description":"\\n\\nДля возможности удалённой демонстрации лабораторий и ученых аудиторий университета был разработан сервис, позволяющий любому пользователю во всех подробностях изучить локации РТУ МИРЭА.\\n\\nДанный сервис позволяет посетить множество локаций РТУ МИРЭА прямо из браузера. За счёт использования сферических снимков пользователь может весьма подробно изучить каждую из них.\\n\\nДля работы с такими экскурсиями был таже разработан конструктор, позволяющий дополнять старые экскурсии и создавать новые.\\n\\nСайт: <a target=\"_blank\" href=\"https://www.mirea.ru/mediapage/a-virtual-tour-of-the-university\">ссылка</a>","images":["/images/projects/Excursion360/resized_1.webp","/images/projects/Excursion360/resized_2.webp"],"videos":[],"tags":["Frontend","Unity","3D"],"tech":["Unity3d","Babylon.js"],"developers":["Борзых Н.Ю.","Макущенко М.А."],"sourcecode":[{"name":"Код","link":"https://github.com/RTUITLab/Excursion360-Builder"}],"link":"Excursion360"},{"#":"","title":"GeoHelper","---":"","description":"\\n\\n\\nПроект для реализации AR-слоя мира, в котором отображаются объекты  дополненной реальности в зависимости от заданного географического  положения.\\n\\nПользователи могут «привязывать» информацию к географическим объектам с помощью телефона, а после просматривать её. Простейшие примеры использования: AR-подсказки для навигации, исторические справки о памятниках архитектуры, справочная информация для коммунальных служб.\\n\\nПроект состоит из сайта и приложений для платформ Android и IOS.\\n\\nСайт предназначен для управления внесёнными данными. Мобильное приложение служит для отображения AR-слоя мира на  экране используемого устройства.\\n\\n","images":["/images/projects/GeoHelperUnity/resized_s1.webp","/images/projects/GeoHelperUnity/resized_s2.webp","/images/projects/GeoHelperUnity/resized_s3.webp","/images/projects/GeoHelperUnity/resized_s4.webp","/images/projects/GeoHelperUnity/resized_s5.webp","/images/projects/GeoHelperUnity/resized_s6.webp","/images/projects/GeoHelperUnity/resized_s7.webp"],"videos":[],"tags":["Frontend","Backend","Mobile","AR"],"tech":["Vue","Node.js","Unity","AR Foundation","C#"],"developers":["Лаптев И.А.","Китанин С.С.","Быковская О.C.","Смирнов Я.А."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/GeoHelper-Front"},{"name":"Бэкенд","link":"https://github.com/RTUITLab/GeoHelper-Back"},{"name":"Unity проект (приложение для Android или Ios)","link":"https://github.com/RTUITLab/GeoHelperUnity"}],"link":"GeoHelperUnity"},{"#":"","title":"GyroBall","---":"","description":"\\n\\nИгра для проверки удобства использования механики акселерометра.\\n\\nИгра состоит из уровней-лабиринтов с препятствиями, через которые необходимо прокатить шар. Управление — наклонами телефона. Для шарика доступно 4 различные механики, соответствующие стихиям: вода, земля, огонь, воздух.\\n\\nЛокации соответствуют выбранной стихийной тематике.","images":["/images/projects/GyroBall/resized_21.webp","/images/projects/GyroBall/resized_22.webp","/images/projects/GyroBall/resized_24.webp","/images/projects/GyroBall/resized_25.webp","/images/projects/GyroBall/resized_Снимок.webp","/images/projects/GyroBall/resized_Снимок2.webp","/images/projects/GyroBall/resized_Снимок3.webp","/images/projects/GyroBall/resized_Снимок4.webp","/images/projects/GyroBall/resized_Снимок5.webp","/images/projects/GyroBall/resized_Снимок22.webp","/images/projects/GyroBall/resized_1.webp","/images/projects/GyroBall/resized_21.webp","/images/projects/GyroBall/resized_2.webp","/images/projects/GyroBall/resized_3.webp"],"videos":[],"tags":["GameDev","Игра","Аркада","Mobile"],"tech":["C#","3D modeling","Unity 3D"],"developers":["Баканова М.В.","Новаков Н.Ю."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/GyroBall"}],"link":"GyroBall"},{"#":"","title":"Лендинг хакатона","---":"","description":"\\n\\nОсенью 2022 года РТУ МИРЭА совместно с ВКонтакте был проведён хакатон для студентов российских вузов. Для обеспечения информационной кампании лабораторией был разработан статический сайт, позволяющий участникам ознакомиться с основными правилами мероприятия, зарегистрироваться на конкурс, а также обратиться за помощью к организаторам.\\n\\nСайт: <a target=\"_blank\" href=\"https://hack.rtuitlab.dev\">hack.rtuitlab.dev</a>","images":["/images/projects/HackLanding/resized_1.webp","/images/projects/HackLanding/resized_2.webp","/images/projects/HackLanding/resized_3.webp"],"videos":[],"tags":["Frontend","Backend"],"tech":["Pug","Python","ServerlessFunctions"],"developers":["Гаршин М.Н.","Лаптев И.А.","Медведев И.В.","Деев Л.Р."],"sourcecode":[{"name":"Репозиторий","link":"https://github.com/RTUITLab/HahathonLanding"}],"link":"HackLanding"},{"#":"","title":"IT-TV (Приложение для интерактивного экрана)","---":"","description":"\\n\\nДанный экран был создан студентами института ИТ. Главной его особенностью является датчик Kinect, который определяет позы людей, находящихся перед ним.\\n\\nРаботая с экраном, студенты института ИТ могут ознакомиться со своим расписанием, узнать последние новости вуза или поиграть в игры, разработанный также студентами института ИТ.","images":["/images/projects/IT-TV/s1.jpeg","/images/projects/IT-TV/s2.jpeg"],"videos":[],"tags":["Информационная система","AR"],"tech":["Unity","C#","Kinect"],"developers":["Макущенко М.А.","Фоменков Ю.И.","Миронов Н.М."],"site":"","sourcecode":[],"link":"IT-TV"},{"#":"","title":"ITLab (система управления лабораторией)","---":"","description":"\\n\\nДля организации работы людей в лаборатории создана система, учитывающая активность людей, в рамках деятельности лаборатории. Система позволяет распределять нагрузку, обеспечивать прозрачность выполняемых действий и мероприятий, контролировать оплату работы.\\n\\nДля использования системы можно использовать браузер, либо специальные мобильные приложения. Поддерживаются уведомления с помощью ботов в социальных сетях и почты.","images":["/images/projects/ITLab/resized_s1.webp","/images/projects/ITLab/resized_s2.webp","/images/projects/ITLab/resized_s3.webp","/images/projects/ITLab/resized_s4.webp","/images/projects/ITLab/resized_1.webp","/images/projects/ITLab/resized_2.webp"],"videos":[],"tags":["Информационная система","Frontend","Backend","Mobile"],"tech":["Vue","ASP","Ktor","Swift"],"developers":["Макущенко М.А.","Романов Д.Е.","Комар Б.Г.","Кузнецов А.А.","Иванов М.Е."],"site":"","sourcecode":[{"name":"Фронтенд","link":"https://github.com/RTUITLab/ITLab-Front"},{"name":"Система событий/оборудования","link":"https://github.com/RTUITLab/ITLab-Back"},{"name":"Система уведомлений","link":"https://github.com/RTUITLab/ITLab-Notify"},{"name":"Система записей о работе","link":"https://github.com/RTUITLab/ITLab-Reports"},{"name":"Система учета зарплат","link":"https://github.com/RTUITLab/ITLab-Salary"},{"name":"Приложение для iOS","link":"https://github.com/RTUITLab/ITLab-iOS"}],"link":"ITLab"},{"#":"","title":"Лендинг лаборатории","---":"","description":"\\n\\nДля демонстрации работы лаборатории был создан сайт, на котором представлены некоторые из разработок лаборатории. Вместе с этим лендинг знакомит с сотрудниками, которые разрабатывают эти проекты. Так как лаборатория активно участвует в мероприятиях различного масштаба, сайт также демонстрирует достижения сотрудников.\\n\\nСайт: <a target=\"_blank\" href=\"https://rtuitlab.dev\">rtuitlab.dev</a>","images":["/images/projects/Landing/resized_1.webp","/images/projects/Landing/resized_2.webp","/images/projects/Landing/resized_3.webp"],"videos":[],"tags":["Frontend"],"tech":["Pug","JavaScript"],"developers":["Баикин К.Е.","Снегирёв Д.А.","Суринов Р.А."],"sourcecode":[{"name":"Репозиторий","link":"https://github.com/RTUITLab/Landing-Front"}],"link":"Landing"},{"#":"","title":"Logistic Map","---":"","description":"\\n\\nДля устройств смешанной реальности Hololens 2 создано многопользовательское приложение, позволяющее производить планирование логистики с помощью размещения маршрутов для различных транспортных средств на цифровой копии карты.","images":["/images/projects/LogisticMap/resized_img1.webp","/images/projects/LogisticMap/resized_img2.webp","/images/projects/LogisticMap/resized_img3.webp","/images/projects/LogisticMap/resized_img4.webp","/images/projects/LogisticMap/resized_img5.webp","/images/projects/LogisticMap/resized_img6.webp"],"videos":[],"tags":["AR","MR","Тактические игры","Unity"],"tech":["C#","Unity","Hololens 2","Mixed Reality Toolkit"],"developers":["Петренко Д.С."],"site":"","sourcecode":[],"link":"LogisticMap"},{"#":"","title":"Малый робот квадрупед","---":"","description":"\\n\\nПервый образец робота квадрупеда (RDS-1), который был создан в лаборатории. Большинство узлов сделано из пластика с применением технологии FDM печати на 3D-принтере.\\n\\nРобот управляется Raspberry Pi 4 с помощью джойстика. Через управляющие ШИМ-сигналы задаются углы 12 двигателей. Вся прошивка написана на Python 3. Робот создан с целью проверки возможности создавать свои робототехнические комплексы внутри лаборатории.","images":["/images/projects/MiniDog/resized_img3.webp","/images/projects/MiniDog/resized_img2.webp","/images/projects/MiniDog/resized_img4.webp","/images/projects/MiniDog/resized_img1.webp"],"videos":[],"tags":["Smart devices","Робототехника"],"tech":["T-Flex CAD","Python"],"developers":["Журавлёв П.Р.","Митницкий А.В.","Дергунов А.А.","Нечипоренко А.Ю."],"site":"","sourcecode":[{"name":"Прошивка","link":"https://github.com/ShiWarai/RDS-1-controller"}],"link":"MiniDog"},{"#":"","title":"Neural Jedi - игра с поддержкой нейроинтерфейса","---":"","description":"\\n\\nКаждый разработчик игр стремится как можно сильнее захватить внимание игрока и на долгие часы погрузить его в виртуальный мир. Лучше всего, пожалуй, сейчас с этим справляются VR-шлемы, буквально переносящие человека в мир игры. Но какой следующий шаг? Разработчики RTUITLab постарались дать ответ на этот вопрос, сказав: “Нейроинтерфейс!”.\\n\\nТак родилась Neural Jedi - небольшая VR-игра, в которой игрок, надев нейроинтерфейс, становится джедаем, который управляет объектами с помощью мозговой активности!\\n\\nПриходилось ли вам часами сидеть за муторной, сложной и, главное, безуспешной работой, после которой хочется только плюнуть на все и со злости что-нибудь разбить? В реальности, к сожалению, без последствий это сделать нельзя, а в Neural Jedi можно.\\n\\nТам вы становитесь школьником или студентом, погружающимся в сон, где вы можете без последствий выпустить пар после тяжелого дня, разрушая все в своей комнате. А так как уничтожать предметы голыми руками может быть не очень интересно, почему бы не сделать это подобно персонажам “Звездных войн”? То есть с помощью светового меча или молнии, выпускаемой из руки. Да и вообще, раз это сон, давайте станем в нем джедаем или ситхом и будем поднимать предметы с помощью Силы!\\n\\nИмитировать известную по фильмам Силу нам как раз и помогает нейроинтерфейс. Считывая сигналы вашего мозга, он определяет, когда вы напряжены, расслаблены или напрягаете только левую руку. Эти три действия и привязаны к основным механикам игры: напрягаясь, вы заставляете подниматься в воздух предметы, на которые смотрите; расслабляясь, вы опускаете их на землю; напрягая только левую руку, вы стреляете из нее молнией. И все без контроллера!\\n\\nТак как перед нами стояла задача полностью погрузить человека в игровой процесс, было принято решение сделать Neural Jedi VR-игрой. Да и что может быть круче, когда ты не только управляешь предметами в игре с помощью своего мозга, но и буквально находишься в игровом мире.\\n\\nВ качестве VR-шлема использовался HTC Vive Pro Eye. Это решение было принято по двум причинам.\\n\\nВо-первых у HTC есть замечательный Vive tracker. Это небольшое устройство, которое просто отслеживает положение чего-либо в пространстве. В игре он используется для отслеживания положения левой руки. Зачем держать контроллер, когда можно стрелять молниями, просто напрягая руку?\\n\\nВо-вторых Vive Pro Eye позволяет отслеживать направление взгляда игрока. Данный функционал используется для определения точки, в которую выстрелит молния из руки. В игре это ощущается настолько естественно, что сперва даже не замечаешь эту фичу.\\n\\nА связать все это нам позволяет гибкий игровой движок Unity. Для создания качественного пост-процессинга в VR используется Universal Render Pipeline.\\n\\nВ Neural Jedi мы используем нейрогарнитуру Emotiv Epoc+. Ее 14 датчиков 128 раз в секунду отправляют на компьютер данные, которые обрабатывает наша нейросеть, чтобы определить, какое действие сейчас хочет совершить игрок.\\n\\nВ итоге нам удалось добиться превосходного уровня погружения в игровой процесс. Да, поначалу управление с помощью нейроинтерфейса кажется непривычным, но со временем перестаешь понимать, как можно было иначе, например, стрелять молнией: нет ничего естественней, чем вытянуть руку и просто напрячь ее.\\n\\nПереведя управление всего двумя игровыми действиями на нейроинтерфейс, мы уже добились неплохих результатов. А что будет, когда мы сумеем полностью раскрыть потенциал этой технологии? Мы полагаем, что использование нейроинтерфейсов в игровой индустрии - это будущее, которое уже стучится в дверь.","images":["/images/projects/NeuralJedi/resized_s1.webp","/images/projects/NeuralJedi/resized_s2.webp","/images/projects/NeuralJedi/g1.gif","/images/projects/NeuralJedi/g2.gif","/images/projects/NeuralJedi/g3.gif"],"videos":[],"tags":["VR","GameDev","Нейроинтерфейс"],"tech":["Unity","C#","Emotiv Epoc+"],"developers":["Красовский М.П."],"site":"","sourcecode":[],"link":"NeuralJedi"},{"#":"","title":"Система тестирования Института ИТ","---":"","description":"\\n\\nДля проведения олимпиад по программированию в РТУ МИРЭА была создан сервис, позволяющий управлять мероприятиями, заданиями, а также производить автоматическое тестирование решений по заранее заготовленным входным данным. Данная система не единожды была использована для проведения Предпрофессиональной олимпиады.\\n\\nСайт: <a target=\"_blank\" href=\"https://olympiad.rtuitlab.ru\">olympiad.rtuitlab.ru</a>","images":["/images/projects/Olympiad/resized_1.webp","/images/projects/Olympiad/resized_2.webp","/images/projects/Olympiad/resized_3.webp"],"videos":[],"tags":["Frontend","Backend"],"tech":["Angular","Asp.net Core"],"developers":["Коновалова С.В.","Лаптев И.А.","Макущенко М.А."],"sourcecode":[{"name":"Код","link":"https://github.com/RTUITLab/Olympiad"}],"link":"Olympiad"},{"#":"","title":"Owl Cabin — Летай-созерцай-собирай-улучшай","---":"","description":"\\n\\nУправляем самолётом от третьего лица. Собираем звёздочки, улучшаем самолёт, набираем очки.\\n\\nКрасивая, спокойная музыка, красочные локации, спокойный полёт.","images":["/images/projects/OwlCabin/resized_s6.webp","/images/projects/OwlCabin/resized_s2.webp","/images/projects/OwlCabin/resized_s3.webp","/images/projects/OwlCabin/resized_s4.webp","/images/projects/OwlCabin/resized_s5.webp","/images/projects/OwlCabin/resized_s1.webp","/images/projects/OwlCabin/resized_s7.webp"],"videos":[],"tags":["VR","GameDev","Игра","Aircraft","Relax","Easy play"],"tech":["Unity","Google Cardboard"],"developers":["Миронов Н. М.","Иерусалимов И. Д.","Корчиков М. Д.","Валяев Д. А.","Шошников И. К."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/OwlCabin"}],"link":"OwlCabin"},{"#":"","title":"The Parallax View - Bathyscaphe","---":"","description":"\\n\\nВы когда‑нибудь задумывались о реальности мира за окном? Мы сидим за столом, едим, спим, мы ощущаем только то, что нас окружает. Окно дает нам свет, показывает мир, который говорит нам, что время не стоит на месте, и что мы все там же где были пару минут назад — в своей комнате. Мы подходим к окну, чтобы взглянуть на зеленые деревья, небо, смотрим на мимо проезжающие машины и как в фильме «Начало» крутим волчок, спим ли мы сейчас?\\n\\nМы - команда разработчиков из лаборатории RTUITLab. Данный проект разрабатывался в течение года и появилось желание о нем рассказать. Возможно у вас появится желание его доработать или предложение внедрить в свою систему.\\n\\nВ данной статье будет рассмотрен вопрос воссоздания системы виртуальной визуализации. Ваш мир за окном скоро перестанет быть реальным!\\n\\nЕсли более техническим языком, то проект описывает применение технологий отслеживания положения тела при помощи инфракрасной камеры глубины и перемещение силуэта в виртуальную среду для изменения окружения.","images":["/images/projects/ParallaxBathyscaphe/resized_img.webp","/images/projects/ParallaxBathyscaphe/gif3.gif","/images/projects/ParallaxBathyscaphe/gif1.gif","/images/projects/ParallaxBathyscaphe/gif2.gif"],"videos":[],"tags":["AR","Компьютерная анимация","3D Vision","3D World"],"tech":["C#","Unity","Java","Orbbec"],"developers":["Запорожских А.И.","Прохоров В.Е."],"site":"","sourcecode":[{"name":"Репозиторий","link":"TheParallaxView"}],"link":"ParallaxBathyscaphe"},{"#":"","title":"PhotoPano","---":"","description":"\\n\\nДанный экран был создан студентами института ИТ. Главной его особенностью является датчик Kinect, который определяет позы людей, находящихся перед ним.\\n\\nРаботая с экраном, студенты института ИТ могут ознакомиться со своим расписанием, узнать последние новости вуза или поиграть в игры, разработанный также студентами института ИТ.","images":["/images/projects/PhotoPano/resized_s1.webp","/images/projects/PhotoPano/resized_s2.webp","/images/projects/PhotoPano/resized_s1.webp"],"videos":[],"tags":["Smart devices","AR","Фотосессия"],"tech":["Kotlin","OrangePi"],"developers":["Запорожских А.И.","Петров И.Р."],"site":"","sourcecode":[],"link":"PhotoPano"},{"#":"","title":"Plane crash simulator","---":"","description":"\\n\\nСимулятор, помещающий пользователя в ситуацию, когда он находится в падающем самолете. Самолет находится в турбулентности и постепенно наклоняется вперед с раскачкой в разные стороны.","images":["/images/projects/PlaneCrashSimulator/resized_1.webp","/images/projects/PlaneCrashSimulator/resized_2.webp","/images/projects/PlaneCrashSimulator/resized_3.webp"],"videos":[],"tags":["VR","Симулятор"],"tech":["Unity","Steam VR","C#"],"developers":["Валяев Д.А.","Шорин И.А."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/RTUITLab/PlainCrashSimulator"}],"link":"PlaneCrashSimulator"},{"#":"","title":"TabletPress","---":"","description":"\\n\\nИгрок занимается сборкой, разборкой, очисткой и настройкой таблеточного пресса и производством на нём таблеток в VR-пространстве.\\n\\nПроект является демонстрацией процесса обучения специалистов по работе с установкой по изготовлению таблеточных форм и производства на нём данных таблеточных изделий: погружение позволяет быстро освоить материал без риска порчи настоящего оборудования и получения травм.","images":["/images/projects/PressTablet/resized_s1.webp","/images/projects/PressTablet/resized_s2.webp","/images/projects/PressTablet/resized_s3.webp","/images/projects/PressTablet/resized_s4.webp","/images/projects/PressTablet/resized_s5.webp","/images/projects/PressTablet/resized_s1.webp"],"videos":[],"tags":["VR","Симулятор","Образовательная"],"tech":["C#","Unity","Blender"],"developers":["Парфёнов Е.И.","Лаптев И.А.","Тескер И.А."],"site":"","sourcecode":[],"link":"PressTablet"},{"#":"","title":"Лаборатория МедИИ","---":"","description":"\\n\\nВ 2021 году для дочерней компании Минпромторга России ФГАУ \"РЦУД и РТ\" был создан интеллектуальный сервис, способный анализировать снимки электронных микроскопов и определять на них наличие опухолей.\\n\\nДанный сервис позволяет загружать снимки тканей, сделанные с помощью электронного микроскопа, а затем подсвечивать разными цветами поверх снимка компоненты тканей, выявленные нейронной сетью.Кроме того, с помощью данного сервиса врач-специалист может самостоятельно оставить комментарии к различным частям снимка, выделив интересующую область.","images":["/images/projects/Rcud/resized_1.webp","/images/projects/Rcud/resized_2.webp"],"videos":[],"tags":["Frontend","Backend","ML"],"tech":["Vue.js","FastAPI","PyTorch"],"developers":["Лаптев И.А.","Левандровский А.М.","Цуканов Д.М."],"sourcecode":[],"link":"Rcud"},{"#":"","title":"Reflex — Система референсных оценок планов лучевой терапии","---":"","description":"\\n\\nВ рамках договора о сотрудничестве с ФГБУ \"НМИЦ ДГОИ им. Дмитрия Рогачева\" Минздрава России был создан сервис, позволяющий проводить оценку планов лучевой терапии несовершеннолетних пациентов со всей России. Данный проект уже запущен в эксплуатацию и активно используется специалистами по назначению.","images":["/images/projects/Reflex/resized_1.webp","/images/projects/Reflex/resized_2.webp","/images/projects/Reflex/resized_3.webp"],"videos":[],"tags":["Frontend","Backend"],"tech":["React","FastAPI"],"developers":["Медведев И.В.","Снегирёв Д.А.","Юрзина С.В."],"sourcecode":[],"link":"Reflex"},{"#":"","title":"Виртуальный ситуационный центр","---":"","description":"\\n\\nВиртуальный ситуационный центр позволяет множеству людей в любой точке планеты взаимодействовать между собой в единой среде виртуальной реальности. При этом возможно использование любого доступного на данный момент шлема виртуальной реальности.\\n\\nСервисом можно пользоваться без шлема, используя клавиатуру и мышь. В сервисе реализована концепция комнат, к которым подключаются люди, и их общение происходит только внутри данного пространства. Разговоры и обмен файлами не выйдут за пределы выделенного пространства, что обеспечивает конфиденциальность приватного общения. Внутри выделенного виртуального пространства люди могут общаться при помощи голосового чата, могут обмениваться документами из облачных хранилищ (OneDrive, Google drive…), просматривать их, комментировать и обсуждать. В данный момент сервис находится на стадии открытого тестирования.","images":["/images/projects/SC/resized_1.webp","/images/projects/SC/resized_2.webp","/images/projects/SC/resized_s1.webp","/images/projects/SC/resized_s2.webp","/images/projects/SC/resized_s3.webp","/images/projects/SC/resized_s4.webp"],"videos":["https://www.youtube.com/embed/HRFRQa3tMcI"],"tags":["VR","Виртуальное рабочее пространство","Social"],"tech":["NodeJS","ASP. net core","Unreal Engine"],"developers":["Макущенко М.А.","Иванов И.Е."],"site":"","sourcecode":[],"link":"SC"},{"#":"","title":"Save the Cat","---":"","description":"\\n\\nСпасти котёнка - расслабляющее и доброе приключение от 3 лица. Помогайте найти жителям предметы. Открывайте новых персонажей. И вместе спасите котёнка. В ваших силах сделать этот мир лучше.\\n\\nВас ждёт: Короткое приключение на 30-60 минут; Добрая игра без конфликтов и сражений; Игра, в которой нельзя проиграть; Сказочная атмосфера.\\n\\nСкачайте игру: <a target=\"_blank\" href=\"https://vkplay.ru/play/game/save_the_cat/\">vkplay</a>\\n\\nПрисоединяйтесь и ощутите магию доброты в этом удивительном игровом мире!","images":["/images/projects/SaveTheCat/resized_0.webp","/images/projects/SaveTheCat/resized_1.webp","/images/projects/SaveTheCat/resized_2.webp","/images/projects/SaveTheCat/resized_3.webp","/images/projects/SaveTheCat/resized_4.webp","/images/projects/SaveTheCat/resized_5.webp","/images/projects/SaveTheCat/resized_6.webp"],"videos":[],"tags":["GameDev","Приключения","Квест"],"tech":["C++","Unreal Engine 4","Blender"],"developers":["Шутов К.И.","Пономарев И.С.","Субботина Л.К.","Сивченков В.А.","Красовский М.П.","Щеглов М.В."],"site":"","sourcecode":[],"link":"SaveTheCat"},{"#":"","title":"Умные шахматы","---":"","description":"\\n\\nIT Академии Samsung в РТУ МИРЭА продолжает радовать нас выпускниками и их проектами! В 2024 году наш Университет стал вузом года по версии Samsung Innovation Campus, а также именно этот проект получил второе место в финале.\\n\\nУмные шахматы совмещают физическую шахматную доску и технологии интернета вещей, что позволяет получить новые ощущения от игры с друзьями по сети. Шахматная доска подключается к платформе LiChess и отображает ходы противника с помощью светодиодов, так что вы всегда знаете, куда сделал ход ваш противник.","images":["/images/projects/SmartChess/resized_chess2.webp","/images/projects/SmartChess/resized_chess1.webp","/images/projects/SmartChess/resized_chess3.webp","/images/projects/SmartChess/resized_chess4.webp"],"videos":[],"tags":["Smart devices","ML","Social","IT Академия Samsung"],"tech":["C++","Python","IoT"],"developers":["Шендяпин А.В.","Левандровский А.М."],"site":"","sourcecode":[],"link":"SmartChess"},{"#":"","title":"Симулятор Сварки","---":"","description":"\\n\\nИгрок занимается сваркой механического элемента в VR-пространстве.\\n\\nБлагодаря тактильным VR перчаткам с отдачей, оператор может максимально погрузиться в процесс сварки механического элемента и за счёт вибраций симулировать отдачу и ошибки во время тренировки оператора.\\n\\nПроект является демонстрацией процесса обучения специалистов по оборудования в VR-пространстве с использованием тактильных перчаток: погружение позволяет быстро освоить материал без риска порчи настоящего оборудования и получения травм.","images":["/images/projects/Svarka/resized_img1.webp","/images/projects/Svarka/resized_img2.webp","/images/projects/Svarka/resized_img3.webp","/images/projects/Svarka/resized_img4.webp"],"videos":[],"tags":["VR","XR","Симулятор","Образовательная"],"tech":["C#","Unity"],"developers":["Макаров Д.В.","Шорин И.А."],"site":"","sourcecode":[],"link":"Svarka"},{"#":"","title":"Undead","---":"","description":"\\n\\nЗахватывающая игра от первого лица.\\n\\nПогрузитесь в мир Undead, захватывающую игру от первого лица с элементами roguelike, где каждое приключение уникально. Ваша цель — вернуть корону. Боритесь с хитрыми гоблинами в мире, где каждый угол может скрывать как смертельную опасность, так и ценный трофей.\\n\\nЧто вас ждёт:\\n\\nНеповторимые Битвы: Осваивайте мастерство ближнего боя. Будьте готовы к быстрым уклонениям и  мощным дальним атакам.\\nУникальные Приключения: Ни одно прохождение не похоже на другое благодаря случайно генерируемым комнатам и сражениям. Исследуйте, сражайтесь и побеждайте, открывая новые стратегии и возможности.\\nВстречи с Гоблинами: Побеждайте разнообразных гоблинов, каждый из которых требует особого подхода. Научитесь предугадывать их движения, чтобы оставаться на шаг впереди.\\n\\nСкачайте игру для Android: <a target=\"_blank\" href=\"https://play.google.com/store/apps/details?id=com.RtuItLab.Undead\">Google Play</a>","images":["/images/projects/Undead/resized_img1.webp","/images/projects/Undead/resized_img2.webp","/images/projects/Undead/resized_img3.webp","/images/projects/Undead/resized_img4.webp","/images/projects/Undead/resized_img5.webp"],"videos":[],"tags":["GameDev","Mobile","Unity","Android"],"tech":["C#","Unity"],"developers":["Шишкин К.Е.","Макаров Д.В.","Тришина Я.И."],"site":"","sourcecode":[{"name":"Исходный код","link":"https://github.com/Pess-dev/UnityDungeonsGame"}],"link":"Undead"},{"#":"","title":"VRTherapy — Приложение для релаксации в VR со множеством разнообразных активностей","---":"","description":"\\n\\nВ VRTherapy вы можете погрузиться в захватывающую и успокаивающую виртуальную реальность с приятным звуковым сопровождением. Отдохните у виртуального костра, прогуляйтесь по летнему лесу, послушайте шум прибоя или же сыграйте в мини-игру, где вам предстоит управлять бумажным самолётиком на ветру. Данная игра предназначена исключительно для запуска в портативном VR-шлеме для Android, например, Google Cardboard.","images":["/images/projects/VR-Therapy/resized_screenshot1.webp","/images/projects/VR-Therapy/resized_screenshot2.webp","/images/projects/VR-Therapy/resized_screenshot3.webp","/images/projects/VR-Therapy/resized_screenshot4.webp"],"videos":[],"tags":["VR","Экспириенс","GameDev","Simulator","Arcade"],"tech":["Unity","Android","Google VR","Google Cardboard"],"developers":["Баканова М.","Левандровский А.","Матчин А.","Новаков Н."],"site":"","sourcecode":[{"name":"Проект","link":"https://github.com/RTUITLab/VRTherapy"}],"разработчики":"","link":"VR-Therapy"},{"#":"","title":"VRDuel — шутер в виртуальной реальности","---":"","description":"\\n\\n\\nШутер в формате виртуальной реальности, позволяющий оттачивать стрельбу из оружия разных видов и уворачиваться от вражеских пуль. В игре можно бросить вызов сразу нескольким противникам, либо сначала потренироваться в спокойной обстановке. В таком режиме у пользователя есть возможность стрелять по бутылкам в баре и мишеням в тире. На выбор представлено 5 моделей оружия, 4 локации и 3 уровня сложности.\\n","images":["/images/projects/VRDuel/resized_148579235-a4f9e25e-dc3c-49d8-b1a6-7dbabfa52a99.webp","/images/projects/VRDuel/resized_148580147-297f79e2-15e3-4b34-8108-9550982b0fe2.webp","/images/projects/VRDuel/resized_148580160-84ee7b40-83f9-439c-a37e-4e1184bd4dd5.webp","/images/projects/VRDuel/resized_148580059-71f366be-9f6c-44bf-a662-abf7e3e00003.webp"],"videos":[],"tags":["Симулятор","Стрельба","VR","GameDev"],"tech":["Unity","C#"],"developers":["Егоркин Н.Е.","Шорин И.А."],"site":"","sourcecode":[{"name":"GitHub","link":"https://github.com/RTUITLab/VRDuel"}],"link":"VRDuel"},{"#":"","title":"ЗИЛ","---":"","description":"\\n\\nИгра для кресла [Futurift](https://6-dof.ru/futurift/).\\n\\nОтрыв от преследования в автомобиле, в зоне боевых действий. Стрельба и оружие прилагаются.","images":["/images/projects/VRProject-3IL/resized_s2.webp","/images/projects/VRProject-3IL/resized_s1.webp","/images/projects/VRProject-3IL/resized_s3.webp","/images/projects/VRProject-3IL/resized_s4.webp"],"videos":[],"tags":["VR","GameDev"],"tech":["Unity","Steam VR","C#"],"developers":["Смирнов М.А.","Новаков Н.Ю.","Егоркин Н.Е.","Валяев Д.А.","Макаров Д.В.","Макущенко М.А.","Баканова М.В.","Шорин И.А.","Иерусалимов И.Д."],"site":"","sourcecode":[{"name":"Source code","link":"https://github.com/RTUITLab/Project-3IL"}],"link":"VRProject-3IL"},{"#":"","title":"Вампиры","---":"","description":"\\n\\nЗахватывающая игра, в которой игрокам предстоит погрузиться в мир тьмы и таинственности, исследуя мрачные локации и сражаясь с врагами, чтобы раскрыть тайны прошлого главной героини, ставшей вампиром.\\n\\nПогрузитесь в мир тьмы и таинственности вместе с главной героиней игры — девочкой, которая просыпается после долгого сна и обнаруживает, что стала вампиром.\\n\\nЕё жизнь кардинально изменилась, и теперь ей предстоит научиться жить с новыми способностями и выживать в мире, где люди боятся и ненавидят таких, как она.\\n\\nВас ждёт захватывающая история, полная тайн, интриг и опасностей. Исследуйте мрачные локации, решайте головоломки и сражайтесь с врагами, чтобы раскрыть все секреты прошлого главной героини и найти своё место в этом новом мире.\\n\\nСможете ли вы пройти через все испытания и сохранить человечность, несмотря на то, что стали вампиром?","images":["/images/projects/Vampires/resized_img1.webp","/images/projects/Vampires/resized_img2.webp","/images/projects/Vampires/resized_img3.webp","/images/projects/Vampires/resized_img4.webp","/images/projects/Vampires/resized_img5.webp"],"videos":[],"tags":["GameDev","Motion Capture","Приключение"],"tech":["C++","Unreal Engine 5","Blender","Motion Capture Vicon Vero"],"developers":["Таран А.Д.","Цветкова Э.А.","Вохрин О.Л.","Орлов Д.А.","Зиновьев Д.П.","Шорин И.А.","Шошников И.К.","Иерусалимов И.Д.","Макаренко П.А.","Ясанкин А.Г.","Орлов Д.А.","Быков И.И.","Тришина Я.И.","Глухов А.А.","Денисенко В.В."],"site":"","sourcecode":[],"link":"Vampires"},{"#":"","title":"ВИКА - Виртуальный информационно-коммуникационный ассистент ИИТ РТУ МИРЭА","---":"","description":"\\n\\nВиртуальный информационно-коммуниационный ассистент для студентов ИИТ.\\n\\nСовместно со специалистами из техно-коворкинга была разработана и внедрена информационная система управления чат-ботом для VK.\\n\\nСистема оперативно рассылает необходимые уведомления старостам групп, а студентам — различные новостные рассылки, расписание занятий и преподователей, расписание пересдач, а также форма обратной связи для оперативных жалоб и пожеланий по улучшению.","images":["/images/projects/Vika/resized_img1.webp","/images/projects/Vika/resized_img2.webp"],"videos":[],"tags":["Backend","ML","Взаимодействие","Чат"],"tech":["C#","ASP.NET Core","PostgreSQL","Python","Rasa"],"developers":["Макущенко М.А.","Парфёнов Е.И.","Карпова А.А."],"site":"","sourcecode":[],"link":"Vika"},{"#":"","title":"ВИКА - Информационный портал института информационных технологий РТУ МИРЭА","---":"","description":"\\n\\nИнформационный портал ВИКИ - на этом сайте собрана полезная и актуальная информация как для абитуриентов, так и для студентов института информационных технологий. Для вашего удобства, мы разделили информацию на несколько страниц и секций по определённым сферам.\\n\\nПортал является частью проекта, по созданию системы сборки сайтов с использованием фабричного метода, инкрементальной регенрации (ISR + Island Architecture), и админского клиента (CMS).\\n\\nАрхитектура проекта из себя представляет смешанную систему, использующую как монолит, в основании которого приложение по сборке сайта на Next.js, так и микросервисы, среди которых например сервис парсинга учебных планов на Express.js и сервис новостей Вики на Spring.\\n\\nСайт: <a target=\"_blank\" href=\"https://vika-it.rtuitlab.dev/\">vika-it.rtuitlab.dev</a>","images":["/images/projects/VikaWebsite/resized_img1.webp","/images/projects/VikaWebsite/resized_img2.webp","/images/projects/VikaWebsite/resized_img3.webp","/images/projects/VikaWebsite/resized_img4.webp","/images/projects/VikaWebsite/resized_img5.webp","/images/projects/VikaWebsite/resized_img6.webp","/images/projects/VikaWebsite/resized_img7.webp"],"videos":[],"tags":["Frontend","Backend","Информационная деятельность","Design"],"tech":["Next.js","React.js","TypeScript","Express.js","Kotlin","Spring","Redis","Figma"],"developers":["Деев Л.Р.","Закатов И.С.","Левандровский А.М.","Макущенко М.А."],"site":"","sourcecode":[],"link":"VikaWebsite"},{"#":"","title":"Голосование \"Студент и преподаватель года\" 2023","---":"","description":"\\n\\nКонкурс «Студент и преподаватель года» МИРЭА — Российского технологического университета проводится с целью выявления и поощрения лучших студентов и преподавателей Университета за активные действия, направленные на развитие образования, науки, спорта, культуры, добровольчества, творчества и общественной деятельности.\\n\\nВеб-приложение «Голосование \"Студент и преподаватель года\" 2023 года» - создано с целью реализации процесса определения лучших участников в рамках двух номинаций: «Студент года» и «Преподаватель года».","images":["/images/projects/VotingStudents2023/resized_img.webp","/images/projects/VotingStudents2023/resized_img_1.webp","/images/projects/VotingStudents2023/resized_img_2.webp"],"videos":[],"tags":["Frontend","Backend","Голосование","Конкурс"],"tech":["React.js","TypeScript","Python"],"developers":["Раздорский В.Ю.","Лаптев И.А."],"site":"","sourcecode":[],"link":"VotingStudents2023"},{"#":"","title":"Народная война (1943): переломный момент","---":"","description":"\\n\\nВ каждой семье детям передаются воспоминания о Великой Отечественной войне — о героическом периоде, наполненном многочисленными жертвами и подвигами, трагедиями и радостями, и завершившемся справедливым итогом.\\n\\nВ рамках подготовки мероприятий к её 80-ти летнему юбилею Институт информационных технологий начал разработку компьютерной игры по мотивам Великой Отечественной войны. Сюжетная линия событий проведёт участника по сценам, составленным по мотивам воспоминаний самих героев.\\n\\nСмотри трейлер игры <a target=\"_blank\" href=\"https://vk.com/video-42869722_456239313\">«Народная война (1943): переломный момент».</a>","images":["/images/projects/Witches/resized_img_4.webp","/images/projects/Witches/resized_img.webp","/images/projects/Witches/resized_img_1.webp","/images/projects/Witches/resized_img_2.webp","/images/projects/Witches/resized_img_3.webp","/images/projects/Witches/resized_img_4.webp","/images/projects/Witches/resized_img_5.webp"],"videos":[],"tags":["GameDev","Трейлер","История","Motion Capture"],"tech":["C++","Unreal Engine 5","Blender","Motion Capture Vicon Vero"],"developers":["Иерусалимов И.Д.","Шошников И.К.","Таран А.Д.","Зиновьев Д.П.","Цветкова Э.А.","Шорин И.А.","Макаренко П.А.","Быков И.И.","Зиновьев Д.П.","Глухов А.А.","Денисенко В.В."],"site":"","sourcecode":[],"link":"Witches"}];
        export default data;
        